import { T_FavoriteCodes } from "shared/components/UnitConverter/types";
import { round, toNumber } from "shared/helpers/number";
import {
  T_ConversionFormulas,
  T_UnitConversionAllUnitsMap
} from "shared/types/unitConverter";

export const unitsMap: T_UnitConversionAllUnitsMap = {
  /**
   * Metric
   */
  mm: {
    name: "Millimeter",
    symbol: "mm"
  },
  cm: {
    name: "Centimeter",
    symbol: "cm"
  },
  m: {
    name: "Meter",
    symbol: "m"
  },
  km: {
    name: "Kilometer",
    symbol: "km"
  },
  /**
   * British Imperial / US customary
   */
  in: {
    name: "Inch",
    symbol: "in"
  },
  ft: {
    name: "Feet",
    symbol: "ft"
  },
  yd: {
    name: "Yard",
    symbol: "yd"
  },
  mi: {
    name: "Mile",
    symbol: "mi"
  }
};

const baseUnit = "m";
const conversionRates = {
  /**
   * Metric
   */
  m: 1,
  mm: 1000,
  km: 0.001,
  cm: 100,
  /**
   * British Imperial / US customary
   */
  in: 39.37008,
  ft: 3.28084,
  yd: 1.093613,
  mi: 0.0006213712
};

const roundingFractionDigits = 7;

const conversionFormulas: T_ConversionFormulas = {
  baseUnitCode: baseUnit,
  fromBaseTo: {
    m: (x: number) => round(toNumber(x), roundingFractionDigits),
    mm: (x: number) => round(toNumber(x * 1000), roundingFractionDigits),
    km: (x: number) => round(toNumber(x * 0.001), roundingFractionDigits),
    cm: (x: number) => round(toNumber(x * 100), roundingFractionDigits),
    in: (x: number) => round(toNumber(x * 39.37008), roundingFractionDigits),
    ft: (x: number) => round(toNumber(x * 3.28084), roundingFractionDigits),
    yd: (x: number) => round(toNumber(x * 1.09361), roundingFractionDigits),
    mi: (x: number) => round(toNumber(x * 0.00062), roundingFractionDigits)
  },
  toBaseFrom: {
    m: (x: number) => round(toNumber(x), roundingFractionDigits),
    mm: (x: number) => round(toNumber(x * 0.001), roundingFractionDigits),
    km: (x: number) => round(toNumber(x * 1000), roundingFractionDigits),
    cm: (x: number) => round(toNumber(x * 0.01), roundingFractionDigits),
    in: (x: number) => round(toNumber(x * 0.0254), roundingFractionDigits),
    ft: (x: number) => round(toNumber(x * 0.3048), roundingFractionDigits),
    yd: (x: number) => round(toNumber(x * 0.9144), roundingFractionDigits),
    mi: (x: number) => round(toNumber(x * 1609.344), roundingFractionDigits)
  }
};

const defaults = {
  inputCode: "ft",
  targetCode: "m",
  favoriteCodes: Object.keys(unitsMap) as T_FavoriteCodes
};

const lengthConstants = {
  unitsMap,
  baseUnit,
  defaults,
  conversionRates,
  conversionFormulas
};

export default lengthConstants;
